<template>
  <div class='wrapper'>
    <div class='wrapleft'>
      <article id='JArticle'>
        <table id='Journal'>
          <tr v-for='journal in journals' v-bind:key='journal.id'>
            <td>
              <div class='tooltip'>
                <img :src='journal.image' alt='' width='24'>
                <span class='tooltiptext'>Fred</span>
              </div>
            </td>
            <td>{{journal.t1}}</td>
            <td>{{journal.t2}}</td>
            <td>{{journal.t3}}</td>
            <td>{{journal.t4}}</td>
          </tr>
        </table>
      </article>
    </div>
      <aside>
      <table id='StarData'>
        <tr v-for='status in statuses' v-bind:key='status.id'>
            <a v-if="status.link === true" class="edsmetcbuttons" :href="status.title" target='_blank'>{{status.text}}</a>
            <td v-if="status.link === false">{{status.title}}</td>
            <td v-if="status.link === false">{{status.text}}</td>
        </tr>
      </table>
    </aside>
    <app-footer @loadMoreRecords='RequestMore'></app-footer>
  </div>
</template>

<script>
import AppFooter from './Footer.vue'
import Socket from '../websocket/socket'

export default {
  props: ['Socket'],
  name: 'Home',
  components: {
    AppFooter
  },
  data () {
    return {
      currentshiptype: '',
      jdata: '',
      statusData: []
    }
  },
  created () {
    Socket.$on('open', this.onOpen)
    Socket.$on('close', this.onClose)
    Socket.$on('message', this.onMessage)
    Socket.$on('error', this.onError)
  },
  beforeDestroy () {
    Socket.$off('open', this.onOpen)
    Socket.$off('close', this.onClose)
    Socket.$off('message', this.onMessage)
    Socket.$off('error', this.onError)
  },
  computed: {
    journals: {
      get () {
        return this.$store.getters['journal/RETRIEVE']
      }
    },
    statuses: {
      get () {
        return this.$store.getters['systemTable/RETRIEVE']
      }
    },
    nextRow: {
      get () {
        return this.$store.getters['journal/NEXT_ROW']
      }
    }
  },
  methods: {
    onOpen (evt) {
      this.currentshiptype = ''
      this.RequestJournal(-1, 50)
      this.RequestStatus(-1)
    },
    onClose (evt) {},
    onMessage (evt) {
      let jdata = JSON.parse(evt.data)
      switch (jdata.responsetype) {
        case 'journalrequest':
          this.$store.dispatch('journal/FILL_DATA', jdata)
          break
        case 'journalpush':
          this.$store.dispatch('journal/FILL_DATA', jdata)
          break
        case 'journalrefresh':
          this.$store.dispatch('journal/CLEAR_JOURNAL')
          this.$store.dispatch('journal/FILL_DATA', jdata)
          break
        case 'status':
          this.$store.dispatch('systemTable/FILL_DATA', jdata)
          break
      }
    },
    onError (evt) {
      console.error(evt.data)
    },
    RequestStatus (entryno) {
      let msg = {
        requesttype: 'status',
        entry: entryno // -1 means send me the latest journal entry first, followed by length others.  else its the journal index
      }

      Socket.send(JSON.stringify(msg))
    },
    RequestIndicator () {
      var msg = {
        requesttype: 'indicator'
      }

      Socket.send(JSON.stringify(msg))
    },
    ClickJournalItem (e) {
      console.log('Clicked' + e.target.tag)
      this.RequestStatus(e.target.tag)
    },
    RequestJournal (start, len) {
      var msg = {
        requesttype: 'journal',
        start: start,
        length: len
      }

      Socket.send(JSON.stringify(msg))
    },
    RequestMore (count) {
      console.log(this.nextRow)
      if (this.nextRow >= 0) {
        this.RequestJournal(this.nextRow, count)
      }
    }
  }
}
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style scoped>
@import '../../WebOld/index.css';
@import '../../WebOld/tooltip.css';
</style>



