<template>
  <div>
    <div class="wrapper">
        <div class="wrapleft">
            <article>
                <table id="StarData">
                  <tr v-for='status in statuses' v-bind:key='status.id'>
                    <a v-if="status.link === true" class="edsmetcbuttons" :href="status.title" target='_blank'>{{status.text}}</a>
                    <td v-if="status.link === false">{{status.title}}</td>
                    <td v-if="status.link === false">{{status.text}}</td>
                </tr>
                </table>
            </article>
        </div>

        <aside>
            <div id="StatusBox">
                <table id="Status"></table>
                <div id="StatusOther"></div>
            </div>
        </aside>

    </div>

    <div id="ActionBox">
        <p>Actions</p>
        <table id="Actions"></table>
    </div>
    <app-footer></app-footer>
  </div>
</template>

<script>
import AppFooter from './Footer.vue'

export default {
  name: 'Status',
  components: {
    AppFooter
  },
  computed: {
    statuses: {
      get () {
        return this.$store.getters['systemTable/RETRIEVE']
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
@import '../../WebOld/Status/status.css';
</style>



